// ---------------------
// ------ INTRO --------
// ---------------------

.intro{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  

  .wrapper{
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin-top: 25.21vh;
  }
}

.intro__title{
  margin-bottom: 30px;
}

.intro__subtitle{
  margin-left: auto;
  @include Mont(500);
  font-size: 24px;
  line-height: 120%;
  margin-bottom: 46px;
  text-align: right;
  color: white;

  span{
    display: block;
  }
}

.intro__arrow-down{
  position: relative;
  animation: shakeArrow 3s infinite ease-in-out;
  ani

  &::after{
    position: absolute;
    content: " ";
    width: 150px;
    height: 150px;
    left: -70px;
    top: 0;
  }
}

@keyframes shakeArrow {
  0%{
    transform: translateY(0);
  }

  50%{
    transform: translateY(30px);
  }

  100%{
    transform: translateY(0);
  }
}


