// ---------------------
// ----- RUNLINE -------
// ---------------------

.runline{
  overflow: hidden;
  padding-top: 100px;
  padding-bottom: 250px;
  margin-bottom: 21px;

  .wrapper{
    width: 1491px;
    width: 100%;
    margin-top: -138px;
  }
}

.runline__line{
  width: fit-content;
  display: flex;

  p{
    font-size: 320px;
    line-height: 432px;
    -webkit-text-stroke: 2px #FFFFFF;
    color: #1F1F1F;
    @include Pix();
  }
}

.runline__desc{
  display: flex;
  margin-left: 50px;
}

.runline__desc_item{
  display: flex;
  flex-direction: column;
  @include Mont(500);
  font-size: 24px;
  line-height: 120%;
  position: relative;
  color: black;

  &::after{
    content: " ";
    position: absolute;
    border-radius: 50%;
    height: 180px;
    filter: blur(70px);
    opacity: .8;
  }
  
  span{
    background: white;
    white-space: nowrap;
    width: fit-content;
    position: relative;
    z-index: 2;
  }

  &.item1{
    margin-right: 134px;

    &:after{
      width: 656px;
      left: -174px;
      top: -76px;
      background: #00FFCB;
    }

    span:nth-child(1){
      margin-left: 24px;
      padding: 6px 22px 3px 22px;
      padding-bottom: 3px;
    }

    span:nth-child(2){
      padding: 0px 22px 8px 21px;
      margin-top: -2px;
      position: relative;
      
      &:after{
        content: "";
        position: absolute;
        left: 100%;
        top: 100%;
        width: 33px;
        height: 33px;
        background: white;
      }
    }
  }

  &.item2{
    margin-right: 167px;

    &:after{
      width: 658px;
      left: -174px;
      top: -73px;
      background: #A597FD;
    }

    span:nth-child(1){
      padding: 5px 22px 4px 16px;
      position: relative;
      
      &:after{
        content: "";
        position: absolute;
        left: 100%;
        bottom: 100%;
        width: 33px;
        height: 33px;
        background: white;
      }
    }

    span:nth-child(2){
      margin-left: 35px;
      margin-top: -6px;
      padding: 3px 22px 5px 13px;
    }
  }

  &.item3{
    &:after{
      width: 737px;
      height: 203px;
      left: -124px;
      top: -87px;
      background: #FF598E;
    }

    span:nth-child(1){
      padding: 5px 22px 1px 21px;
      margin-left: auto;
    }

    span:nth-child(2){
      margin-top: -4px;
      padding: 3px 17px 4px 24px;
      margin-right: 35px;
      position: relative;
      
      &:after{
        content: "";
        position: absolute;
        left: 100%;
        top: 100%;
        width: 35px;
        height: 35px;
        background: white;
      }
    }
  }
}


